import {usersAPI} from "../api/api";
import {updateObjectInArray} from "../utils/object-helpers";

const FOLLOW = 'FOLLOW';
const UNFOLLOW = 'UNFOLLOW';
const SET_USERS = 'SET_USERS';
const SET_CURRENT_PAGE = 'SET_CURRENT_PAGE';
const SET_TOTAL_USER_COUNT = 'SET_TOTAL_USER_COUNT';
const TOGGLE_IS_LOADING = 'TOGGLE_IS_LOADING';
const TOGGLE_IS_FOLLOWING_PROGRESS = 'TOGGLE_IS_FOLLOWING_PROGRESS';

let initialState = {
    usersData: [],
    pageSize: 20,
    totalUsersCount: 0,
    currentPage: 1,
    isLoading: false,
    isFollowingInProgress: [],


}

const usersReducer = (state = initialState, action) => {

    if (action.type === FOLLOW) {
        return {
            ...state,
            usersData: updateObjectInArray(state.usersData,action.id,'id',{followed: true})
        };


    } else if (action.type === UNFOLLOW) {
        return {
            ...state,
            usersData: updateObjectInArray(state.usersData,action.id,'id',{followed: false})
        };
    } else if (action.type === SET_USERS) {
        return {
            ...state,
            usersData: action.users
        }
    } else if (action.type === SET_CURRENT_PAGE) {
        return {
            ...state,
            currentPage: action.currentPage
        }
    } else if (action.type === SET_TOTAL_USER_COUNT) {
        return {
            ...state,
            totalUsersCount: action.totalCount
        }
    } else if (action.type === TOGGLE_IS_LOADING) {
        return {
            ...state,
            isLoading: action.isLoading
        }
    } else if (action.type === TOGGLE_IS_FOLLOWING_PROGRESS) {
        return {
            ...state,
            isFollowingInProgress: action.isFollowingInProgress ?
                [...state.isFollowingInProgress, action.id] :
                state.isFollowingInProgress.filter(id => id !== action.id)

        }
    } else {

        return state;

    }

}

export const unfollowSuccess = (userid) => ({type: UNFOLLOW, userid});

export const followSuccess = (userid) => ({type: FOLLOW, userid});

export const setUsers = (users) => ({type: SET_USERS, users});

export const setCurrentPage = (currentPage) => ({type: SET_CURRENT_PAGE, currentPage});

export const setTotalUsersCount = (totalCount) => ({type: SET_TOTAL_USER_COUNT, totalCount});

export const setIsLoading = (isLoading) => ({type: TOGGLE_IS_LOADING, isLoading});

export const setFollowingProgress = (isFollowingInProgress, id) => ({
    type: TOGGLE_IS_FOLLOWING_PROGRESS,
    isFollowingInProgress,
    id
});

export const requestUsers = (page, pageSize) => async (dispatch) => {
    dispatch(setIsLoading(true));
    dispatch(setCurrentPage(page));

    let data = await usersAPI.getUsers(page, pageSize)

    dispatch(setIsLoading(false));
    dispatch(setUsers(data.items));
    dispatch(setTotalUsersCount(data.totalCount));


}


const followUnfollowFlow = async (dispatch, id, apiMethod, actionCreator) => {
    dispatch(setFollowingProgress(true, id));

    let resultCode = await apiMethod(id);

    if (resultCode === 0) {
        dispatch(actionCreator(id));
    }
    dispatch(setFollowingProgress(false, id));
}

export const unfollow = (id) => async (dispatch) => {
    followUnfollowFlow(dispatch, id, usersAPI.unfollow.bind(usersAPI), unfollowSuccess)
}

export const follow = (id) => async (dispatch) => {
    followUnfollowFlow(dispatch, id, usersAPI.follow.bind(usersAPI), followSuccess)

}

export default usersReducer;

