import s from "./UserData.module.css";
import ProfileStatus from "./ProfileStatus/ProfileStatus";

const UserData = ({profile, updateStatus, status}) => {

    return (
        <div>

            <div className={s.userData}>
                <div className={s.userImg}>
                    <img src={profile.photos.large || 'https://caronrentals.com/images/user.png'} alt=""/>
                </div>
                <div>
                    <div className={s.userName}><span>{profile.fullName}</span></div>

                    <ProfileStatus status={status} updateStatus={updateStatus}/>

                    <div>
                        contacts:
                        <p>Facebook:{profile.contacts.facebook}</p>
                        <p>vk:{profile.contacts.vk}</p>
                        <p>twitter:{profile.contacts.twitter}</p>
                        <p>instagram:{profile.contacts.instagram}</p>
                        <p>github:{profile.contacts.github}</p>
                    </div>
                    <div>
                        <p>IS looking for Job:{profile.lookingForAJob ? 'yes' : 'no'}</p>
                        <p>Description:{profile.lookingForAJobDescription}</p>
                    </div>
                </div>
            </div>
        </div>
    );


}

export default UserData;