import s from './SiteBar.module.css'
import { NavLink } from "react-router-dom";
import Friends from "./Friends/Friends";
import React from "react";
import { connect } from "react-redux";
import { toggleShowingSideBar } from "../../Redux/sideBar-reducer";
import l from '../../assets/img/logo.png'

const SiteBar = (props) => {
   return (
      <div className={props.showSideBar ? s.siteBar : s.none}>
         <div className={s.LBWrapper}>
            <div onClick={() => { props.toggleShowingSideBar() }} className={s.burger}><span ></span></div>
            <img src={l} className={s.logo} alt="" />
         </div>

         <ul className={s.list}>
            <li className={s.item}><NavLink onClick={() => { props.toggleShowingSideBar() }} activeClassName={s.activeLink} to="/profile">Profile</NavLink></li>
            <li className={s.item}><NavLink onClick={() => { props.toggleShowingSideBar() }} activeClassName={s.activeLink} to="/messages">Messages</NavLink></li>
            <li className={s.item}><NavLink onClick={() => { props.toggleShowingSideBar() }} activeClassName={s.activeLink} to="/news">News</NavLink></li>
            <li className={s.item}><NavLink onClick={() => { props.toggleShowingSideBar() }} activeClassName={s.activeLink} to="/music">Music</NavLink></li>
            <li className={s.item}><NavLink onClick={() => { props.toggleShowingSideBar() }} activeClassName={s.activeLink} to="/settings">Settings</NavLink></li>
            <li className={s.item}><NavLink onClick={() => { props.toggleShowingSideBar() }} activeClassName={s.activeLink} to="/users">Find Users</NavLink></li>

            <li className={s.item}><Friends firendsData={props.friends} /></li>
         </ul>
      </div>
   )
};




const mapStateToProps = (state) => {
   return {
      friends: state.siteBar.friends,
      showSideBar: state.siteBar.showSideBar
   }
}

export default connect(mapStateToProps, { toggleShowingSideBar })(SiteBar)

