const TOGGLE_SHOWING_SIDEBAR = 'TOGGLE_SHOWING_SIDEBAR';


let initialState = {
    friends: [
        {id: 1, name: 'Andrew', img: 'https://w3schoolsrus.github.io/html/img_girl.jpg'},
        {id: 2, name: 'Dmitriy', img: 'https://w3schoolsrus.github.io/html/img_girl.jpg'},
        {id: 3, name: 'Sasha', img: 'https://www.cagbd.org/newdesign/assets/img/mission.jpg'}
    ],
    showSideBar: false
};


const sideBarReducer = (state = initialState, action) => {
    if (action.type === TOGGLE_SHOWING_SIDEBAR) {
        return {
            ...state,
            showSideBar: !state.showSideBar
        }
    }else {
        return state;
    }

}

export const toggleShowingSideBar = () => ({type: TOGGLE_SHOWING_SIDEBAR});

export default sideBarReducer;