import s from './Posts.module.css'
import Post from "./Post/Post";
import React from "react";
import {Field, reduxForm} from "redux-form";
import {maxLengthCreator, requiredField} from "../../../utils/validators/validators";
import {InputCreator} from "../../common/FormsControls/FormsControls";

let maxLength = maxLengthCreator(10)
const Textarea = InputCreator('textarea')
const PostForm = (props) => {


    return (
        <form onSubmit={props.handleSubmit} className={s.userPosts}>
            <p>My posts</p>
            <div className={s.newPost__form}>

                <Field placeholder='your news...' name={'post'} component={Textarea}
                       validate={[requiredField, maxLength]}/>

                <button>Send</button>
            </div>

        </form>
    )
}

const PostsReduxForm = reduxForm({form: 'ProfileAddNewPostForm'})(PostForm)


const Posts = React.memo((props) => {
    const onSubmit = (formData) => {
        props.newPost(formData.post);
    }


    let posts = [...props.data].reverse().map(p => {
        return <Post mesenge={p.message} key={p.id} likeCounter={p.likes}/>;
    });
    return (
        <>
            <PostsReduxForm onSubmit={onSubmit}/>
            {posts}
        </>
    );
});


export default Posts;