import s from './Header.module.css'
import l from '../../assets/img/logo.png'
import m from '../../assets/img/messages-icon.png'
import b from '../../assets/img/bellicon.png'
import u from '../../assets/img/users.png'


import { NavLink } from "react-router-dom";
import React from "react";


const Header = (props) => {




   return (
      <div className={s.header}>


         <div onClick={() => {
            props.toggleShowingSideBar();

         }} className={s.burger}><span></span></div>
         <img className={s.logo} src={l} alt="" />
         <div className={s.iconsNawBarWrap}>


            <div className={s.iconsNawBar}>
               <div><NavLink to="/messages"><img className={s.icon} src={m} alt="" /></NavLink></div>
               <div><NavLink to="/users"><img className={s.icon} src={u} alt="" /></NavLink></div>
               <div><img className={s.icon} src={b} alt="" /></div>
            </div>
         </div>


         {props.isAuth ?
            <div className={s.loginName}>{props.login}
               <button className={s.logout} onClick={props.logout}>logout</button>
            </div> :
            <div className={s.loginBlock}>
               <div><NavLink to={'/login'}>Login</NavLink></div>
            </div>
         }
      </div>

   )
}

export default Header;