import React from 'react'
import s from './FormsControls.module.css'
import {Field} from "redux-form";

export const InputCreator = (InputType) => ({input,meta, ...props}) => {
    const hasError = meta.touched && meta.error
    return (
        <div className={s.formControl + " " + (hasError ? s.error : ' ')}>
            <div className={s.textareaWrapper}>
                <InputType  {...props} {...input}/>
            </div>

            {hasError && <span>{meta.error}</span>}

        </div>
    )
}


export const createField = (validate, placeholder, name, component, type = 'text', props = {}) => {
    return (
        <Field type={type} validate={validate} placeholder={placeholder} name={name} component={component} {...props}/>

    )
}

