import React, {Suspense} from "react";
import './App.css';
import SiteBar from "./components/SiteBar/SiteBar";

import News from "./components/News/News";
import Music from "./components/Music/Music";
import Settings from "./components/Settings/Settings";
import {BrowserRouter, Route, withRouter} from "react-router-dom"

import ProfileContainer from "./components/Profile/ProfileContainer";
import HeaderContainer from "./components/Header/HeaderContainer";


import {connect, Provider} from "react-redux";
import {logout} from "./Redux/auth-reducer";
import {initialiseApp} from "./Redux/app-reducer";
import {Component} from "react/cjs/react.production.min";
import Preloader from "./components/common/Preloader/Preloader";
import {compose} from "redux";
import store from "./Redux/redux-store";
import {LazyLoadingComponent} from "./hoc/withSuspense";


//lazy

const MessagesContainer = React.lazy(() => import('./components/Messages/MessagesContainer'));
const UsersContainer = React.lazy(() => import('./components/Users/UsersContainer'));
const Login = React.lazy(() => import('./components/Login/Login'));


class App extends Component {
    componentDidMount() {
        this.props.initialiseApp()

    }


    render() {
        if (!this.props.initialised) {
            return <Preloader/>
        }
        return (


            <div className='wrapper'>
                <HeaderContainer/>
                <SiteBar/>
                <div className="content-wrp">


                    <Route path={'/profile/:userId?'} render={() => <ProfileContainer/>}/>

                    <Route path={'/messages'} render={LazyLoadingComponent(MessagesContainer)}/>

                    <Route path={'/users'} render={LazyLoadingComponent(UsersContainer)}/>

                    <Route path={'/news'} component={News}/>

                    <Route path={'/music'} component={Music}/>

                    <Route path={'/settings'} component={Settings}/>

                    <Route path={'/login'} render={LazyLoadingComponent(Login)}/>
                </div>
            </div>

        );
    }
}

const mapStateToProps = (state) => {
    return {
        initialised: state.app.initialised,
        siteBar: state.siteBar
    }

}


let AppContainer = compose(connect(mapStateToProps, {initialiseApp, logout}),
    withRouter
)(App)

const SamuraiJSApp = () => {
    return (
        <BrowserRouter>
            <Provider store={store}>
                <AppContainer/>
            </Provider>
        </BrowserRouter>
    )
}

export default SamuraiJSApp;



