import s from '../UserData.module.css';
import React, {useState, useEffect} from "react";


const ProfileStatus = (props) => {


    const [editMode, setEditMode] = useState(false);
    const [status, setStatus] = useState(props.status);


    useEffect(() => {
        setStatus(props.status)
    }, [props.status]);

    const activateEditMode = () => {
        setEditMode(true);
    };
    const deactivateEditMode = () => {
        setEditMode(false);
        props.updateStatus(status);
    };

    const onStatusChange = (e) => {
        setStatus(e.currentTarget.value)

    };

    return (
        <div className={s.status}>
            {!editMode ?
                <div>
                    <span onClick={activateEditMode} className={s.statusText} data-testid="statusText">{props.status||'-----'}</span>
                </div>
                :
                <div>
                    <input data-testid={'statusTextInput'} onChange={onStatusChange} onBlur={deactivateEditMode} autoFocus={true}
                           type="text" value={status}/>
                </div>}


        </div>
    );


};

export default ProfileStatus;